.projects {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 80vh;
  width: 90vw;
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.projects span:nth-of-type(1) {
  color: var(--black);
  font-size: 2rem;
  font-weight: bold;
}
.projects .swiper {
  overflow: visible !important;
}
.projects-slider {
  margin-top: 3rem;
  width: 100%;
}
.projects-slider .swiper-slide {
  width: 20rem;
}
.projects img {
  width: 19rem;
  filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
  border-radius: 19px;
}
